import Decimal from 'decimal.js-light';
import { curry } from './utils';

/**
 * 
 * [0.1, 1) 0
 * [0.01, 0.1) -1
 * [0.001, 0.01) -2
 *
 * @param  {Number} value 
 * @return {Integer} 
 */
function getDigitCount(value: number) {
  let result;

  if (value === 0) {
    result = 1;
  } else {
    result = Math.floor(new Decimal(value).abs().log(10).toNumber()) + 1;
  }

  return result;
}

/**
 * [start, end)
 * js
 *
 * @param  {Decimal} start 
 * @param  {Decimal} end   
 * @param  {Decimal} step  
 * @return {Array}         
 */
function rangeStep(start: Decimal, end: Decimal, step: Decimal) {
  let num = new Decimal(start);
  let i = 0;
  const result = [];

  // magic number to prevent infinite loop
  while (num.lt(end) && i < 100000) {
    result.push(num.toNumber());

    num = num.add(step);
    i++;
  }

  return result;
}
/**
 * 
 *
 * @param  {Number} a  
 * @param  {Number} b  
 * @param  {Number} t  [0, 1]
 * @return {Number}    
 */
const interpolateNumber = curry((a: number, b: number, t: number) => {
  const newA = +a;
  const newB = +b;

  return newA + t * (newB - newA);
});
/**
 * 
 *
 * @param  {Number} a 
 * @param  {Number} b 
 * @param  {Number} x 
 * @return {Number}   x a ~ b[0, 1]
 */
const uninterpolateNumber = curry((a: number, b: number, x: number) => {
  let diff = b - +a;

  diff = diff || Infinity;

  return (x - a) / diff;
});
/**
 * 
 *
 * @param  {Number} a 
 * @param  {Number} b 
 * @param  {Number} x 
 * @return {Number}   x a ~ b[0, 1]
 * x a ~ b a ~ b 
 */
const uninterpolateTruncation = curry((a: number, b: number, x: number) => {
  let diff = b - +a;

  diff = diff || Infinity;

  return Math.max(0, Math.min(1, (x - a) / diff));
});

export { rangeStep, getDigitCount, interpolateNumber, uninterpolateNumber, uninterpolateTruncation };
